import { h, Component } from 'preact'
import { useState } from 'preact/hooks'

interface CounterProps {
  label_: string
  initialValue_: number
}

interface CounterState {
  value_: number
}

export class CounterClass extends Component<CounterProps, CounterState> {
  state: CounterState

  constructor(props: CounterProps) {
    super(props)
    this.state.value_ = props.initialValue_
  }

  increment_ = () => this.setState({ value_: this.state.value_ + 1 })
  decrement_ = () => this.setState({ value_: this.state.value_ - 1 })

  render() {
    return <div class="counter">
      <h1>{this.props.label}</h1>
      <p>
        <button onClick={this.decrement_}>-</button>
        {' '}
        {this.state.value_}
        {' '}
        <button onClick={this.increment_}>+</button>
      </p>
    </div>
  }
}

export let CounterFunction = (props: CounterProps) => {
  let [value, setValue] = useState(props.initialValue_)
  return <div class="counter">
    <h1>{props.label_}</h1>
    <p>
      <button onClick={() => setValue(value - 1)}>-</button>
      {' '}
      {value}
      {' '}
      <button onClick={() => setValue(value + 1)}>+</button>
    </p>
  </div>
}
